export class Beep {

    // mp3/wav converter to base64
    // https://dopiaza.org/tools/datauri/index.php

    public static beep = () => {
        var snd = new Audio("data:audio/wav;base64,");  
        snd.play();
    }

    public static beepError() {
        var snd = new Audio("data:audio/wav;base64,");
        snd.play();
    }
}